<template>
  <div id="register">
    <div class="regWarp">
       <p>如果你已拥有账号,即可在此<span class="login" @click="login">登录</span></p>
      <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
      <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
      <mt-field label="手机号" placeholder="请输入手机号" type="number"  v-model="phone" :state="state"></mt-field>
      <mt-field label="验证码" v-model="code">
        <div class="login-code" @click="refreshCode">
    <!--验证码组件-->
    <sidentify :identifyCode="identifyCode"></sidentify>
  </div>
      </mt-field>
      <mt-button type="primary" size="large" class="regBtn" @click="registerNow">立即注册</mt-button>
    </div>
  </div>
</template>
<script>
import sidentify from './sidentify.vue'
import { Toast } from 'mint-ui';
export default {
  name: 'register',
  data () {
    return {
      identifyCodes: "1234567890",
      identifyCode: "",
      code:"",//text框输入的验证码
      username:"",
      password:"",
      phone:"",
      account:[],
      state:""
    }
  },
  components: {
    sidentify
  },
  created () {
    this.refreshCode()
  },
methods: {
  //切换到登录页
  login(){
    this.$router.push('/login')
  },
  //验证码
randomNum(min, max) {
 return Math.floor(Math.random() * (max - min) + min);
},
  
refreshCode() {
 this.identifyCode = "";
 this.makeCode(this.identifyCodes, 4);
},

makeCode(o, l) {
 for (let i = 0; i < l; i++) {
 this.identifyCode += this.identifyCodes[
  this.randomNum(0, this.identifyCodes.length)
 ];
 }
},

registerNow(){
  console.log(this)
   let re = /^1\d{10}$/;
  // let str = this.phoneValue;
  //          if(re.test(str)){
  //              alert('成功')
  //          }else {
  //              alert('抱歉手机号不合法')
  //         }
  if(!this.username.trim()){
      Toast('账号不能为空');
  }else if(!this.password.trim()){
      Toast('密码不能为空');
  }else if(!this.phone.trim()){
      Toast('请输入手机号')       
  }else if(!re.test(this.phone)){
      Toast('手机号不正确')
  }else if(!this.code.trim()){
      Toast('请输入验证码')
  }else if(this.identifyCode !== this.code){
          console.log(this.identifyCode)
          this.refreshCode()
          Toast('输入的验证码不正确')
  }else{
      let userList = localStorage.getItem("account");      
        if(userList){
            userList = JSON.parse(userList)
            this.account = userList
        }
            
             this.account.push({
                username: this.username,
                password:this.password,
                phone: this.phone
             })
             localStorage.setItem("account", JSON.stringify(this.account));
      Toast('注册成功,跳转登录页')
      setTimeout(() => {
        this.$router.push('/login')
      }, 3000);       
  }
},
},
 mounted(){
 this.identifyCode = "";
 this.makeCode(this.identifyCodes, 4);
},
}
</script>
<style scoped>
#register{
  width: 100%;
  height: 100%;
  background: url("../../assets/login4.jpg") no-repeat 50% 50%;
  background-size: cover;
}
.regWarp{
    width: 260px;
    height: 330px;
    border-radius: 10px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    background: rgb(250 250 250 / 0.3);
    box-shadow: 0px 1px 1px 1px #060747;
    padding: 20px;
}
.regWarp>p{
    text-align: center;
    margin-bottom: 30px;
    font-size: 13px;
    text-shadow: 1px 1px 1px #888;
}
.regWarp .login{
    color: #f44336;
    font-weight: bolder;
}
.regWarp .mint-cell{
    margin-bottom: 10px;
    box-shadow: 0px 1px 1px 1px #626290;
    border-radius: 10px;
}
/* .regWarp .mint-cell:nth-child(1){
  margin-top: 50px;
} */
.regWarp .regBtn{
  margin-top: 20px;
}
.regWarp .mint-cell-wrapper{
  flex: 0.5;
}
.login-code{
 cursor: pointer;
}
</style>