<template>
  <div class="phone">
    <div class="warp">
      <div class="phoneCheck">
        <h2>手机号码归属地查询</h2>
        <mt-field
          label="手机号"
          placeholder="请输入手机号"
          type="number"
          v-model="phone"
        ></mt-field>
        <mt-button type="danger" size="large" @click="queryPone"
          >查询</mt-button
        >
      </div>
      <div class="record">
        <h4>查询记录</h4>
        <ul class="poneList">
          <li v-for="(obj, index) in poneList" :key="index">
            <span class="pName">{{ obj.phone }}</span>
            <span class="time">{{ obj.datas }}</span>
            <button class="see" @click="seePone(obj)">查看</button>
          </li>
        </ul>
        <div :class="['noPone', poneList.length >= 1 ? 'isShow' : '']">
          暂无记录
        </div>
      </div>
    </div>
    <div class="Mask" v-show="isShow" @click="clerMask">
      <div class="result">
        <h4>查询结果</h4>
        <ul class="information">
          <li v-for="(item, index) in phoneInfor" :key="index">
            <p>{{ item.title }}</p>
            <p>{{ item.name }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
export default {
  name: "Phone",
  data() {
    return {
      phone: "",
      isShow: false,
      key: "0538af51f9b00e7343563bb272dee982",
      poneList: [], // 记录信息
      phoneInfor: [], // 查询结果信息
    };
  },
  created() {
    let phoneArr = localStorage.getItem("poneList");
    if (phoneArr) {
      phoneArr = JSON.parse(phoneArr);
      this.poneList = phoneArr;
    }
  },
  methods: {
    apiPone(bool, phone) {
      ///mobile/get  0538af51f9b00e7343563bb272dee982
      this.$axios({
        method: "get",
        url: `/dev/mobile/get?phone=${phone}&key=${this.key}`,
      }).then((res) => {
        let dataObj = res.data;
        if (bool) {
          let myDate = new Date();
          setTimeout(() => {
            this.isShow = true;
            this.poneList.unshift({
              phone: this.phone,
              datas: myDate.toLocaleString(),
            });
            localStorage.setItem("poneList", JSON.stringify(this.poneList));
            this.phone = "";
          }, 1500);
        }
        this.phoneInfor = [
          {
            title: "手机号码",
            name: phone,
          },
          {
            title: "省份",
            name: dataObj.result.province
              ? dataObj.result.province
              : "暂无信息",
          },
          {
            title: "城市",
            name: dataObj.result.city ? dataObj.result.city : "暂无信息",
          },
          {
            title: "区号",
            name: dataObj.result.areacode
              ? dataObj.result.areacode
              : "暂无信息",
          },
          {
            title: "邮编",
            name: dataObj.result.zip ? dataObj.result.zip : "暂无信息",
          },
          {
            title: "运营商",
            name: dataObj.result.company ? dataObj.result.company : "暂无信息",
          },
        ];
      });
    },

    queryPone() {
      if (!this.phone.trim()) {
        Toast("手机号不能为空");
      } else {
        this.apiPone(true, this.phone);
      }
    },
    seePone(obj) {
      //this.phone = obj.phone;
      this.apiPone(false, obj.phone);
      this.isShow = true;
    },
    clerMask() {
      this.isShow = false;
    },
  },
};
</script>
<style scoped>
.phone {
  padding-top: 50px;
}
.warp {
  padding: 10px 20px;
}
.phoneCheck h2 {
  text-align: center;
  margin-bottom: 10px;
}
.phoneCheck .mint-cell {
  min-height: 40px;
  border-radius: 6px;
}
.phoneCheck .mint-button {
  margin-top: 10px;
  border-radius: 10px;
}
.record {
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #888;
  box-shadow: 0px 1px 10px 5px #888;
}
h4 {
  padding: 5px 0px;
  text-align: center;
  border-bottom: 1px solid #888;
}
.poneList {
  height: 300px;
  overflow: auto;
}
.record .poneList li {
  padding: 10px 5px;
  border-bottom: 1px solid #888;
  display: flex;
  align-items: center;
}
.pName,
.time {
  flex: 1;
  font-size: 13px;
}
.see {
  padding: 2px;
  background: #26a2ff;
  color: #fff;
}
.isShow {
  display: none;
}
.noPone {
  padding: 30px;
  text-align: center;
  font-size: 15px;
}
.Mask {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: #33333338;
}
.Mask .result {
  width: 300px;
  height: 360px;
  background: #fafafa;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  padding: 10px;
  box-shadow: 1px 1px 17px 5px #333;
}
.result .information {
  padding: 10px;
}
.information li {
  display: flex;
  align-items: center;
  line-height: 50px;
}
.information li p {
  border-bottom: 1px solid #c8c8cd;
  text-align: center;
}
.information li p:nth-child(1) {
  flex: 1;
  border-right: 1px solid #c8c8cd;
}
.information li p:nth-child(2) {
  flex: 2;
}
</style>


