<template>
  <div class="postcode">
    <div class="warp">
      <h2>邮政编码查询</h2>
      <ul class="mailToland">
        <li class="name1">
          <span>邮编查询地名</span>
        </li>
        <li class="name2">
          <input type="text" placeholder="输入邮编" v-model="postcode" />
          <button @click="mailQuery">查询</button>
        </li>
      </ul>
      <ul class="landTomail">
        <li class="name1">
          <span>地址查询邮编</span>
        </li>
        <li class="name2">
          <ul class="landList">
            <li>
              <Areaselect ref="landChild"></Areaselect>
            </li>
            <li>
              <button class="landQuery" @click="LandQuery">查询</button>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!-- 查询记录 -->
    <div class="record">
      <h4>查询记录</h4>
      <ul>
        <li>
          <span>根据邮编查询</span>
          <button @click="mailRecord">查看记录</button>
        </li>
        <li>
          <span>根据地址查询</span>
          <button>查看记录</button>
        </li>
      </ul>
    </div>

    <!-- 弹窗(查询结果) -->
    <div class="Mask" v-show="isShow" @click="clerMask">
      <div class="result">
        <h4>查询结果</h4>
        <ul class="information">
          <li v-for="(item, index) in codeList" :key="index">
            <p>{{ item.title }}</p>
            <p>{{ item.name.toString() }}</p>
          </li>
        </ul>
      </div>
    </div>

    <!-- 记录信息弹窗 -->
    <div class="recordInfor" v-show="isrecord" @click="clerMask">
      <div class="recordPop">
        <h4>查询记录</h4>
        <ul class="poneList">
          <li v-for="(item, index) in mailList" :key="index">
            <span class="pName">{{ item.name }}</span
            ><span class="time">{{ item.datas }}</span
            ><button class="see" @click="seeRecord(item)">查看</button>
          </li>
        </ul>
        <div class="noPone" v-show="isBool">暂无记录</div>
      </div>
    </div>
  </div>
</template>
<script>
import Areaselect from "../components/common/areaselect";
import { Toast } from "mint-ui";
/*
api: 
邮编: /api/postcode/query
区域列表: /api/postcode/pcd
地名: /api/postcode/search
key: 93097e26d3b2023adfbec4522c81c087
*/
export default {
  name: "Postcode",
  data() {
    return {
      key: "93097e26d3b2023adfbec4522c81c087",
      postcode: "",
      codeList: null,
      isShow: false,
      isrecord: false,
      mailList: [],
      landList: [],
      isBool: false,
      landList:[]
    };
  },
  created() {
    let mailArr = localStorage.getItem("mailList");
    if (mailArr) {
      this.isBool = false;
      mailArr = JSON.parse(mailArr);
      this.mailList = mailArr;
    } else {
      this.isBool = true;
    }
  },
  components: {
    Areaselect,
  },
  methods: {
    apiMail(bool, mail) {
      this.$axios({
        method: "get",
        url: `/api/postcode/query?postcode=${mail}&key=${this.key}`,
      }).then((res) => {
        let data = res.data;
        console.log(data);
        let obj = data.result.list[0];
        let AddressList = [];
        data.result.list.map((item) => {
          AddressList.push(item.Address);
        });
        this.codeList = [
          {
            title: "邮编",
            name: obj.PostNumber,
          },
          {
            title: "省份",
            name: obj.Province,
          },
          {
            title: "市区",
            name: obj.City,
          },
          {
            title: "县级",
            name: obj.District,
          },
          {
            title: "乡镇",
            name: AddressList,
          },
        ];

        AddressList = [];

        console.log(this.codeList);
        if (bool) {
          let myDate = new Date();
          setTimeout(() => {
            this.isShow = true;
            this.mailList.unshift({
              name: obj.PostNumber,
              datas: myDate.toLocaleString(),
            });
            localStorage.setItem("mailList", JSON.stringify(this.mailList));
            this.postcode = "";
          }, 1500);
        } else {
          this.isShow = true;
          this.postcode = "";
        }
      });
    },
    clerMask() {
      this.isShow = false;
      this.isrecord = false;
    },
    mailQuery() {
      if (!this.postcode.trim()) {
        Toast("请输入邮编号码");
      } else {
        this.apiMail(true, this.mailList);
      }
    },
    seeRecord(obj) {
      this.apiMail(false, obj.name);
    },
    mailRecord() {
      this.isrecord = true;
    },
    LandQuery(){
      console.log(this.$refs.landChild.landList)
      this.landList = this.$refs.landChild.landList
      let pid = this.landList[0].index,
          cid = this.landList[1].index,
          did = this.landList[2].index;
      if(pid == ''){
        Toast("请选择省级")
      }else if(cid == ''){
        Toast("请选择市级")
      }else{
        this.$axios.get(`/api/postcode/search?pid=${pid}&did=${did}&cid=${cid}&key=${this.key}`).then(res=>{
                            console.log(res)
        })
      }      
    }
  },
};
</script>
<style scoped>
.postcode {
  padding: 50px 10px;
}
.warp {
  background: #eaeaea;
  border: 1px solid #888;
  box-shadow: 1px 1px 15px 3px #888;
  margin-top: 20px;
}
.warp h2 {
  line-height: 40px;
  text-align: center;
  background: cornflowerblue;
}
h4 {
  text-align: center;
}
.warp .mailToland,
.landTomail {
  /* padding: 0px 10px; */
  display: flex;
  align-items: center;
}

.warp .name1 {
  flex: 1;
  text-align: center;
  border-right: 1px solid #888;
  border-bottom: 1px solid #888;
}
.warp .name2 {
  flex: 2;
  text-align: center;
}
.mailToland .name2 {
  border-bottom: 1px solid #888;
}
.mailToland .name2 input {
  height: 30px;
  padding-left: 5px;
}
.mailToland .name2 button {
  width: 60px;
  height: 30px;
  border-radius: 5px;
  margin-left: 10px;
  color: #fff;
  background: #26a2ff;
}
.mailToland {
  line-height: 70px;
}

.landTomail {
  height: 250px;
}
.landTomail .name1 {
  height: 250px;
  line-height: 250px;
}
.landList {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.landList .landQuery {
  width: 100px;
  height: 30px;
  color: #fff;
  border-radius: 5px;
  background: #26a2ff;
}

.Mask,
.recordInfor {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: #33333338;
}
.Mask .result {
  width: 300px;
  height: 360px;
  background: #fafafa;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  padding: 10px;
  box-shadow: 1px 1px 17px 5px #333;
}
.result .information {
  padding: 10px;
}
.information li {
  display: flex;
  align-items: center;
  line-height: 50px;
}
.information li p {
  border-bottom: 1px solid #c8c8cd;
  text-align: center;
}
.information li p:nth-child(1) {
  flex: 1;
  border-right: 1px solid #c8c8cd;
}
.information li p:nth-child(2) {
  flex: 2;
}
.information li:last-child {
  line-height: inherit;
}
.information li:last-child p:nth-child(1) {
  line-height: 138px;
}

.information li:last-child p:nth-child(2) {
  height: 138px;
  line-height: 25px;
  overflow: auto;
}

.record {
  background: #fff;
  margin-top: 20px;
  box-shadow: 1px 1px 8px 4px #888;
}

.record h4 {
  line-height: 20px;
}

.record ul li {
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.record ul li button {
  width: 100px;
  height: 30px;
  color: #fff;
  border-radius: 5px;
  background: #26a2ff;
}

.recordPop {
  width: 90%;
  margin: 30% auto;
  box-shadow: 0px 1px 22px 5px rgb(0 0 0 / 70%);
  background: #fdfdfd;
}
h4 {
  padding: 5px 0px;
  text-align: center;
  border-bottom: 1px solid #888;
}
.poneList {
  height: 300px;
  overflow: auto;
}
.recordPop .poneList li {
  padding: 10px 5px;
  border-bottom: 1px solid #888;
  display: flex;
  align-items: center;
}
.pName,
.time {
  flex: 1;
  font-size: 13px;
}
.see {
  padding: 2px;
  background: #26a2ff;
  color: #fff;
}
.noPone {
  padding: 30px;
  text-align: center;
  font-size: 15px;
}
</style>